import { Component, OnInit, Input } from '@angular/core';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-cre002-cr',
  templateUrl: './cre002-cr.component.html',
  styleUrls: ['./cre002-cr.component.sass']
})
export class Cre002CrComponent implements OnInit {

  @Input() payload: any;

  constructor() { }

  ngOnInit() {
  }

  valueMask(value: string, mask: string = '.0'): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', '¢ ', mask);
  }
}
