import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { SendInformationService } from 'src/app/modules/common/service/SendInformation/send-information.service';
import { ValidationRulesService } from 'src/app/modules/common/service/validationRules/validation-rules.service';
import { CatalogoService } from 'src/app/modules/common/service/catalogo/catalogo.service';
import { Errores } from './errores';
import {
  PAIS, MODULO, LENGUAJE, MODAL, PROPERTY,
  zonaGeograficaNivel1, zonaGeograficaNivel2, zonaGeograficaNivel3
} from 'src/app/modules/common/CONST';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { InfoModalTMAComponent } from 'src/app/modules/common/components/entryComponents/info-modal-tma/info-modal-tma.component';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-cre001-sv',
  templateUrl: './cre001-sv.component.html',
  styleUrls: ['./cre001-sv.component.scss']
})
export class Cre001SvComponent implements OnInit {

  disabled: boolean;
  zonaGeograficaNivel1: string = zonaGeograficaNivel1;
  zonaGeograficaNivel2: string = zonaGeograficaNivel2;
  zonaGeograficaNivel3: string = zonaGeograficaNivel3;

  @Input() formulario: FormGroup;
  // tslint:disable-next-line: no-input-rename
  @Input('observable') obser: SendInformationService<any>;


  @Input() esCliente: boolean;
  @Input() PAISES: any;
  @Input() ESTADO_CIVIL: any;
  @Input() ACTIVIDAD_LABORAL: any;
  @Input() NIVEL_EDUCATIVO: any;
  @Input() TIPO_VIVIENDA: any;
  @Input() LUGARES_DE_TRABAJO: any;
  @Input() CARGOS: any;
  departamento: any;
  @Input() set DEPARTAMENTOS( v: any ) {
    this.departamento = v.map((p: any) => Object.assign({value: p.codigo, label: p.nombre}, p));
  }
  get DEPARTAMENTOS(): any {
    return this.departamento;
  }
  @Output() ngMUNICIPIOS = new EventEmitter<Array<any>>();
  @Input() MUNICIPIOS: any;

  set catDepartamentos(v) {
    this.DEPARTAMENTOS = v;
  }

  set catCiudades(v) {
    this.MUNICIPIOS = v;
  }

  @Input() rules: ValidationRulesService;
  @Input() params: any;

  @Input() catalogo: CatalogoService;
  contratos: {
    TERMINOS_CONDICIONES_PRODUCTO_CREDITO: any;
  };

  errores: any;

  constructor() {
    this.disabled = true;
  }

  ngOnInit() {
    this.contratos = {
      TERMINOS_CONDICIONES_PRODUCTO_CREDITO: {}
    };
    this.errores = new Errores().get(this.rules);
    this.catalogo.data('TERMINOS_CONDICIONES_PRODUCTO_CREDITO', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.contratos.TERMINOS_CONDICIONES_PRODUCTO_CREDITO = response[0];
    });
    this.formulario.get('ingresosMensuales').disable();
  }

  openModal(KEY: string, controlName: string, e?: any): void {
    if (!e || e.target.tagName !== 'A') { return; }
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: (close) => {
            this.formulario.get(controlName).setValue(true);
            close();
          },
          buttonText: 'Aceptar',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: this.contratos[KEY].title,
      message: this.contratos[KEY].message,
      entryComponent: InfoModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }

  setValue(item: any): string {
    return item.value;
  }

  setLabel(item: any): string {
    return item.label;
  }

  filtro(item: any): string {
    return item.label;
  }

  getCatalogoGeoV2(KEY: string, property?: string, value?: string, pais?: string): void {
    if ((property !== 'ICD' && value) || (value.split(':')[0].length > 0 && value.split(':')[1].length > 0)) {
      const params = this.params([PAIS, LENGUAJE]);
      if (property) {
        params.property = property;
      }
      if (value) {
        const data = value.split(':');
        if (data[1]) {
          if (Number.isNaN(Number(data[1]))) {
            return;
          }
        } else if (Number.isNaN(Number(value))) {
          return;
        }
      }
      params.modulo = 'CTACAM';
      this.catalogo.data(`${KEY}${value ? '/' + value : ''}`, params).subscribe(response => {
        this.MUNICIPIOS = response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p));
        this.ngMUNICIPIOS.emit(this.MUNICIPIOS);
      });
    }
  }

  int(field: string): number {
    return parseInt(this.formulario.get(field).value || 0, 10);
  }

  mascaraNumeros(value: string): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', '$ ', '.2');
  }

}
