import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Cre001Component } from './cre001/cre001.component';
import { Cre002Component } from './cre002/cre002.component';
import { Cre003Component } from './cre003/cre003.component';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';

const routes: Routes = [
  {
    path: 'cre001',
    component: Cre001Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE004'
    }
  },
  {
    path: 'cre002',
    component: Cre002Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE012'
    }
  },
  {
    path: 'cre003',
    component: Cre003Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE005'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DatosCreditoRoutingModule { }
