import { Component, OnInit, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { SendInformationService } from 'src/app/modules/common/service/SendInformation/send-information.service';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { InfoModalTMAComponent } from 'src/app/modules/common/components/entryComponents/info-modal-tma/info-modal-tma.component';
import { MODAL, PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';
import { Errores } from './errores';
import { ValidationRulesService } from 'src/app/modules/common/service/validationRules/validation-rules.service';
import { CatalogoService } from 'src/app/modules/common/service/catalogo/catalogo.service';

@Component({
  selector: 'app-cre001-cr',
  templateUrl: './cre001-cr.component.html',
  styleUrls: ['./cre001-cr.component.scss']
})
export class Cre001CrComponent implements OnInit {

  @Input() formulario: FormGroup;
  // tslint:disable-next-line: no-input-rename
  @Input('observable') obser: SendInformationService<any>;
  @Input() ACTIVIDAD_LABORAL: any;
  @Input() PROVINCIA: any;
  @Input() CONDICION_VIVIENDA: any;
  @Input() TIPO_DOCUMENTO: any;
  @Input() PARENTESCO: any;
  @Input() rules: ValidationRulesService;
  @Input() params: any;
  @Input() NIVEL_EDUCATIVO: any;
  @Input() FUENTEINGRESOS: any;
  @Input() PROFESIONES: any;
  @Input() OCUPACIONES: any;

  @Input() catalogo: CatalogoService;
  contratos: {
    TERMINOS_CONDICIONES_PRODUCTO_CREDITO: any;
  };

  errores: any;

  constructor() { }

  ngOnInit() {
    this.contratos = {
      TERMINOS_CONDICIONES_PRODUCTO_CREDITO: {}
    };
    this.errores = new Errores().get(this.rules);
    this.catalogo.data('TERMINOS_CONDICIONES_PRODUCTO_CREDITO', this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.contratos.TERMINOS_CONDICIONES_PRODUCTO_CREDITO = response[0];
    });
  }

  openModal(KEY: string, controlName: string, e?: any): void {
    if (!e || e.target.tagName !== 'A') { return; }
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: ( close ) => {
            this.formulario.get(controlName).setValue(true);
            close();
          },
          buttonText: 'Aceptar',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: this.contratos[KEY].title,
      message: this.contratos[KEY].message,
      entryComponent: InfoModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }
}
