import { Component, OnInit, Input } from '@angular/core';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-cre002-hn',
  templateUrl: './cre002-hn.component.html',
  styleUrls: ['./cre002-hn.component.sass']
})
export class Cre002HnComponent implements OnInit {

  @Input() payload: any;

  constructor() { }

  ngOnInit() {
  }

  valueMask(value: string): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', 'L ', '.2');
  }

}
