import { Component, OnInit, Input } from '@angular/core';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-cre002-sv',
  templateUrl: './cre002-sv.component.html',
  styleUrls: ['./cre002-sv.component.sass']
})
export class Cre002SvComponent implements OnInit {

  @Input() payload: any;

  constructor() { }

  ngOnInit() {
  }

  valueMask(value: string, mask: string = '.0'): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', '$ ', mask).replace(/\,/g, '.');
  }
}
